.container
{
    margin: auto;
   text-align: center;
    width: 80%;
}
 
.contact__form
{
    display: flex;
    justify-content: space-evenly;
    align-items: center;
}

.input__field{
    background-color: transparent;
    margin:2rem 0;
    width: 20rem;
    height: 2rem;
    border: 0;
    outline: none;
    border-bottom: 2px solid white;
    color: white;
    transition: all 0.5s;
}
.input1
{
    width: 80%;
    height: 100%;
}

.input2
{
    width: 80%;
}


.contact__primary__heading
{
    font-size: 5rem;
}

.contact__details
{
    font-size: 2rem;
    margin: 1rem 0;
}

@media all and (max-width:900px) {
    .contact__form{
        flex-direction: column;
    }
}

.input__field:focus{
    transition: 300ms ease all;
    border-bottom: 2px solid red;
}